.footer{
    width: 100%;
    background: #032139;
}
.footer .inner{
    width: 1300px;
    padding: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .inner .logo{
    width: 200px;
}
.footer .inner .logo h2 a{
    display: block;
    width: 140px;
}
.footer .inner .logo h2 a img{
    width: 100%;
    margin-bottom: 40px;
}
.footer .inner .logo button{
    background: #fff;
    color: #07B7E0;
    font-size: 16px;
    padding: 12px 27px;
    border-radius: 8px;
    font-weight: 700;
}
.footer .inner .footer_gnb{
    display: flex;
    gap: 20px;
}
.footer .inner .footer_gnb li>h4{
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 20px;
}
.footer .inner .footer_gnb .depth2{
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.footer .inner .footer_gnb .depth2 li{
    font-size: 12px;
    font-weight: 400;
}