.modal{
    position: fixed;
    left: 0; top: 0;
    width: 100%;
    height: 100%;
    background: #000000c2;
    z-index: 200;
}
.modal .modal_wrap{
    width: 900px;
    height: 700px;
    position: absolute;
    top: 50%; left: 50%; transform: translate(-50%,-50%);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 20px;
    overflow: hidden;
}
.modal .modal_wrap .content{
    width: 100%;
    height: 100%;
    position: relative;
    padding: 60px !important;
    justify-content: flex-end !important;
    background: linear-gradient(0deg, #fff 10%, rgba(0, 0, 0, 0) 75%);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.modal .modal_wrap .content>span{
    width: 24px; height: 24px;
    transform: scale(1.5);
    position: absolute;
    top: 0; right: 0;
    margin-right: 40px;
    margin-top: 40px;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    cursor: pointer;
}
.modal .modal_wrap .content>span:hover{
    transform: rotate(180deg) scale(2);
}
.modal .modal_wrap .content>span svg path{
    fill: #07B7E0;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.modal .modal_wrap .content>span:hover svg path{
    fill: #fff;
}
.modal .modal_wrap .content .title{
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
}

.modal .modal_wrap .content .title h3{
    font-size: 40px;
}
.modal .modal_wrap .content .title h4{
    font-size: 20px;
    font-weight: 500;
    color: #555;
}
.modal .modal_wrap .content .title p{
    font-size: 15px;
    line-height: 1.2;
}
.modal .modal_wrap .content .info_wrap{
    border-top: 1px solid #bababa;
    display: flex;
    padding-top: 12px;
}
.modal .modal_wrap .content .info_wrap ul{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 6px;
}
.modal .modal_wrap .content .info_wrap ul li{
    color: #555;
    font-size: 14px;
    cursor: default !important;
    font-weight: 500;
}
.modal .modal_wrap .content .info_wrap ul li>span{
    margin-left: 12px;
    font-weight: 500;
    color: #111;
    position: relative;
    font-size: 15px;
    font-weight: 600;
}
.modal .modal_wrap .content .info_wrap ul li>span::before{
    content: '';
    width: 1px; height: 8px; position: absolute; 
    top: 50%; left: 0; transform: translateY(-50%);
    margin-left: -8px;
    background: #ccc;
}