.movielist{
    width: 100%;
    padding: 40px;
}
.movielist .title{
    display: flex;
    gap: 20px;
    margin-bottom: 24px;
    align-items: center;
}
.movielist .title h2{
    font-size: 24px;
    font-weight: 700;
}
#tab{
    display: flex;
    outline: 1px solid #04223A;
    border-radius: 100px;
}
#tab button{
    padding: 8px 20px;
    border-radius: 100px;
    color: #04223A;
    font-weight: 600;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
#tab button:hover{
    color: #04223A;
    background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
    color: transparent;
    -webkit-background-clip: text;
}
#tab button#clicked{
    background: #04223A;
    color: #fff;
}




.swiper-scrollbar{
    background: #f6f6f6 !important;
}
.swiper-scrollbar-drag{
    background: #ddd !important;
}
.swiper-scrollbar-drag:hover{
    background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%) !important;
}
.swiper-button-prev{
    width: 60px !important;
    height: 100% !important;
    left: 0 !important;
    top: -30px !important;
    background: linear-gradient(270deg, rgba(255, 255, 255, 0.00) 0%, #FFF 90%);
}
.swiper-button-next{
    width: 60px !important;
    height: 100% !important;
    right: 0 !important;
    top: -30px !important;
    background: linear-gradient(-270deg, rgba(255, 255, 255, 0.00) 0%, #FFF 90%);
}
.swiper-button-prev::after,
.swiper-button-next::after{
    color: rgba(30,213,169, 1);
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.swiper-button-prev:hover::after,
.swiper-button-next:hover::after{
    color: rgba(1,180,228, 1);
}


.movielistbg .swiper{
    background-image: url(https://www.themoviedb.org/assets/2/v4/misc/trending-bg-39afc2a5f77e31d469b25c187814c0a2efef225494c038098d62317d923f8415.svg);
    background-repeat: no-repeat;
    background-position: center 100px;
}

.movielistbgran{
    padding: 0;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.movielistbgran>div{
    padding: 60px;
    box-sizing: border-box;
    background: linear-gradient(to right, rgba(3, 37, 65, .75) 0%, rgba(3, 37, 65, .75) 100%);
}
.movielistbgran .swiper-button-prev,
.movielistbgran .swiper-button-next{
    top: 8px !important;
    background:none;
}
.movielistbgran .title h2{
    color: #fff;
}
.movielistbgran .swiper-scrollbar{
    background: #f6f6f660 !important;
}