.ads{
    width: 100%;
    box-sizing: border-box;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    filter:contrast(250%);
    filter:saturate(200%);
}
.ads .wrap2{
    background: #2c025390;
    width: 100%;
    padding: 60px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
}
.ads .wrap2 h2{
    font-size: 30px;
    color: #fff;
}
.ads .wrap2 p{
    color: #fff;
}
.ads .wrap2 button{
    padding: 12px 24px;
    background: #663b8f;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.ads .wrap2 button:hover{
    background: #4a276b;
}