.header{
    width: 100%;
    position: fixed;
    z-index: 100;
    background: linear-gradient(to right, #032541 100%, #032541 100%);
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.header a,li{
    color: #fff;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    position: relative;
}
.left_header>ul>li::after{
    content: ''; position: absolute; left: 0;  bottom: -2px; z-index: 101;
    width: 0px; height: 2px; border-radius: 100px;
    background: linear-gradient(to left, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
    transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.left_header>ul>li:hover::after{
    width: 120px;
    background: linear-gradient(to left, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
}
.header .inner{
    width: 1300px;
    height: 60px;
    padding: 0 40px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header .inner h1>a{
    display: block;
    width: 140px;
    font-size: 0px;
}
.header .inner .left_header,
.header .inner .right_header{
display: flex;
align-items: center;
gap: 20px;
}


.header .inner .left_header>ul,
.header .inner .right_header>ul{
display: flex;
gap: 20px;
}

.header .inner .left_header>ul>li{
    position: relative;
    height: 30px;
    line-height: 30px;
}
.header .inner .left_header>ul>li>.depth2{
    border: 1px solid #ccc;
    position: absolute; top: 0px; left: 0;
    z-index: 100;
    margin-top: 30px;
    width: 120px;
    display: flex;
    flex-direction: column;
    padding: 8px 0;
    background: #fff;
    border-radius: 0 0 4px 4px;
    opacity: 1;
    transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.header .inner .left_header>ul>li>.depth2_show{
    top: -200px; opacity: 0;
    transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
    display: none;
}
.header .inner .left_header>ul>li>.depth2>li{
    color: #032541;
    font-weight: 500;
    padding: 0 8px;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.header .inner .left_header>ul>li>.depth2>li:hover{
    background: #ebebeb;
}
.header2{
    background: #fff;
    background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
}
.header2::after{
    position: absolute;
    content: '';
    width: 0%; left: 50%;
    transform: translateX(-50%);
    height: 4px;
    animation: header_bottom 1.5s cubic-bezier(0.075, 0.82, 0.165, 1) .5s forwards;
    background: linear-gradient(to left, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
}
@keyframes header_bottom {
    0%{width: 0%;}
    100%{width: 100%;}
}
.header2 h1>a>img{
    filter: brightness(200%);
}
.header2 .search svg path{
    stroke: #fff;
}
.right_header ul{
    align-items: center;
}
.right_header .lang{
    border: 1px solid #ccc;
    padding: 2px 6px;
}
.right_header .login{
    position: relative;
    padding: 8px 0;
}
.right_header .login:hover::after{
    opacity: 1;
    content: '로그인하기!';
    width: 100px;
    text-align: center;
    position: absolute; bottom: 0; left: 50%; z-index: 110;
    transform: translateX(-50%);
    margin-bottom: -34px;
    padding: 8px;
    border-radius: 100px;
    background: #ffffff50;
    animation: toast_message 1.5s cubic-bezier(0.19, 1, 0.22, 1) infinite;
}
@keyframes toast_message {
    0%{margin-bottom: -34px;}
    50%{margin-bottom: -38px;}
    100%{margin-bottom: -34px;}
}