.banner{
    padding-top: 40px;
    width: 100%;
    height: 420px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    filter:contrast(250%);
    filter:saturate(200%);
}
.wrap {
    width: 100%;
    height: 100%;
    padding: 60px;
    display: flex;
    gap: 8px;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    background: rgba(7, 68, 92, 0.6);
}
.wrap h2{
    font-size: 44px;
    color: #fff;
    font-weight: 700;
}
.wrap h3{
    font-size: 28px;
    color: #fff;
}
.wrap form{
    width: 100%;
    position: relative;
    margin-top: 20px;
}
.wrap form input{
    width: 100%;
    padding: 12px;
    border-radius: 100px;
    outline: none;
    border: none;
    text-indent: 8px;
}
.wrap form input:focus{
    outline: 2spx solid #07B7E0;
}
.wrap form input::placeholder{
    color: #ccc;
}
.wrap form button{
    position: absolute;
    right: 0;
    width: 120px;
    height: 100%;
    padding: 10px 26px;
    border-radius: 100px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.wrap form button:hover{
    font-size: 18px;
    font-weight: 600;
    background: linear-gradient(to left, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
}