.login{
    width: 100%;
}
.login .inner{
    width: 1300px;
    padding: 120px 60px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 24px;
}
.login .inner h2{
    font-size: 28px;
    margin-bottom: 20px;
}
.login .inner p{
    line-height: 1.3;
    color: #333;
}
.login .inner p span{
    color: #01b4e4;
    font-weight: 500;
    cursor: pointer;
}

.mb-3{
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.mb-3:first-child{
    margin-bottom: 20px;
}
.mb-3 label{
    font-weight: 500;
}
.mb-3 input{
    border: none;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 12px;
}
.mb-3 input:focus{
    border: 1px solid #01b4e4;
}

.btn_wrap{
    display: flex;
    gap: 12px;
    align-items: center;
}
.btn_wrap button{
    padding: 8px 22px;
    background: #01b4e4;
    border-radius: 8px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.btn_wrap button:hover{
    background: #032541;
}
.btn_wrap span{
    font-size: 14px;
    color: #01b4e4;
    cursor: pointer;
}