.card{
    margin-top: 20px;
    width: 150px;
    min-width: 150px;
    padding-bottom: 40px;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    cursor: pointer;
}
.card::after{
    content: ''; opacity: 0;
    position: absolute; top: 0; left: 0; z-index: -1;
    width: 90%; height: 65%;
    border-radius: 6px;
    background: linear-gradient(to bottom, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.card:hover::after{
    opacity: 1;
    top: 18px; left: 24px; 
}

.card2{
    margin-top: 20px;
    width: 280px;
    min-width: 280px;
    position: relative !important;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    cursor: pointer;
}
.card2 .play_btn{
    position: absolute;
    top: 30%;  left: 50%; transform: translateX(-50%) !important;
}
.card2 .imgbox{
    width: 100%; height: 170px !important;
}
.card2 .imgbox img{
    width: 100%; height: 100% !important;
}
.card2 .contentbox{
    width: 100%;
    height: 56px;
    position: absolute; bottom: 0;
    margin-bottom: 40px;
    display: flex; align-items: center; gap: 0px !important;
    text-align: center;
    background: linear-gradient(0deg, #000 10%, rgba(0, 0, 0, 0.00) 100%);
    border-radius: 0 0 8px 8px;
}
.card2 .contentbox h4{
    color: #fff;
}
.card2 .contentbox p{
    color: #ccc !important;
}

.card:hover,
.card2:hover{
    transform: scale(1.08);
}


.card .imgbox{
    width: 100%;
    height: 225px;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .1);
}
.card .imgbox img{
    width: 100%; height: 100%;
}
.card .contentbox{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2px;
    margin-top: -20px;
    padding: 0 10px ;
}
.card .contentbox .rate{
    height: 34px; width: 34px;
    display: flex; align-items: center;
    border-radius: 100%;
    background-color: #081c22;
    position: relative;
    margin-bottom: 8px;
}
.card .contentbox .rate>p{
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    text-indent: -6px;
    letter-spacing: -1px;
}
.card .contentbox .rate>p span{
    display: block;
    position: absolute; right: 6px; top: 11px;
    color: #fff;
    font-size: 7px;
}
.card .contentbox .rate::before{
    content: '';
    width: 82%;
    height: 82%;
    position: absolute; left: 2px; top: 2px;
    border: 1px solid #30C07A;
    border-radius: 100%;
}
.card .contentbox>h4{
    width: 100%; 
    height: 22px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.card .contentbox>p{
    font-size: 14px;
    color: #555;
}